import * as React from 'react';
import { Redirect, Route, Switch } from 'react-router';
import { BrowserRouter } from 'react-router-dom';
import App from './App';
import Mike from './Pages/Mike';

const DefaultRedirect = () => <Redirect to="/" />;

export const routes = {
  home: {
    mike: "/mike",
  },
};

// Here we can add routes to different pages (urls)
const RootRouter = () => (
  <BrowserRouter basename="/jorisvdsluis.github.io">
      <Switch>
        <Route exact path="/" component={App} />
        <Route path="/wanneerkomtmikeweerterug" component={Mike} />
      </Switch>
  </BrowserRouter>
);


export default RootRouter;