import React from "react";
import { Image, Segment} from "semantic-ui-react";
import mike from "../assets/mike.jpg";
import Countdown from './Countdown';
import CountdownMobile from './CountdownMobile';
class Mike extends React.Component {
    constructor(props) {
        super(props);
        this.state = {width: window.innerWidth}}
        componentWillMount() {
            window.addEventListener("resize", this.handleWindowSizeChange);
          }
        
          componentWillUnmount() {
            window.removeEventListener("resize", this.handleWindowSizeChange);
          }
    handleWindowSizeChange = () => {
        this.setState({ width: window.innerWidth });
      };
      state = {};
  render() {
      
    const { width } = this.state;
    const isMobile = width <= 500;
    const currentDate = new Date();
    console.log(width)
    const year = (currentDate.getMonth() === 4 && currentDate.getDate() > 23) ? currentDate.getFullYear()  : currentDate.getFullYear();
 
    if(isMobile){
        return(
<>
<Image src={mike}/>
<CountdownMobile date={`2020-04-24T00:00:00`} />
</>
        );

    }else{
    return (
<>

<Countdown date={`2020-04-24T00:00:00`} />
        
<Image src={mike}/>

</>
    );
  }}
}

export default Mike;
